<template>
	<div class="main">
		<div type="button" class="layui-btn" @click="$router.push({path:'/home/table_sub/form_new'})"></div>
		<div class="el-tables">
			<el-table :data="tableData" style="width: 100%" row-key="id">
				<el-table-column prop="date" label="" width="180">
				</el-table-column>
				<el-table-column prop="name" label="" width="180">
				</el-table-column>
				<el-table-column prop="address" label="">
				</el-table-column>
			</el-table>
		</div>
		<div class="dg-wrap">
			<draggable v-model="vdtest.list"  chosenClass="chosen" forceFallback="true" animation="1000" @start="onStart" @end="onEnd">
				<transition-group>
					<div v-for="(v,i) in vdtest.list" :key="v.id">{{v.id}}</div>
				</transition-group>
			</draggable> 
		</div>
	</div>
</template>

<script>
	import { defineComponent } from 'vue'//
	import { VueDraggableNext } from 'vue-draggable-next'
	export default defineComponent({//export default {  export default defineComponent({
		data() {
			return {
				tableData: [{
					date: '2016-05-02',
					name: '',
					address: ' 1518 '
				}, {
					date: '2016-05-04',
					name: '',
					address: ' 1517 '
				}, {
					date: '2016-05-01',
					name: '',
					address: ' 1519 '
				}, {
					date: '2016-05-03',
					name: '',
					address: ' 1516 '
				}],
				//vue-draggable-test
				drag:false,
				vdtest:{
					list:[]
				}
			}
		},
		components:{
			draggable:VueDraggableNext,
		},
		mounted() {
			this.rowDrop();
			//vue-draggable-test
			this.getdata()
		},
		methods: {
			//
			rowDrop() {
				const tbody = document.querySelector(".el-table__body-wrapper tbody");
				const _this = this;
				Sortable.create(tbody, {
					// ,,
					animation: 150,
					// ghostClass: "blue-background-class",
					onEnd({
						newIndex,
						oldIndex
					}) {
						_this.tableData.splice(newIndex, 0, _this.tableData.splice(oldIndex, 1)[0]);
						var newArray = _this.tableData.slice(0);
						_this.tableData = [];
						_this.$nextTick(function(){
							_this.tableData = newArray;
						})
					}
				});
			},
			//vuedraggable
			//
			getdata(){
				var _this = this
				setTimeout(function() {
					_this.vdtest.list = [
						{
							id:1
						},
						{
							id:2
						},
						{
							id:3
						},
						{
							id:4
						},
					]					
				},500)
			},
			//
			onStart(){
				this.drag=true;
			},
			//
			onEnd() {
				this.drag=false;
			},
		},
	})
</script>

<style scoped="scoped">
	.main {
		padding-top: 20px;
	}
	.layui-btn {
		margin: 0 20px 20px;
	}
	.el-tables,
	.dg-wrap {
		margin: 0 20px 20px;
		padding: 20px;
		background-color: white;
	}
</style>
